body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: rgba(139, 136, 136, 0.692);
  position:fixed;
    top:0;
    width:100%;
    height:100%;
    overflow-y:auto;

}

.mainList {
  background-color: rgba(0, 0, 0, 0.856);
margin-top:0;
list-style: none;
text-align: left;


}
.mainItem  {
  margin-right: 20px;
display: inline-block;
margin:20px;

margin-bottom: 20px;

}


.link {
  text-decoration: none;
  color:white;
  font-family: sans-serif;
font-size:20px;
}

.link:hover {
  color: rgb(228, 0, 0);

}

#blabla {
  display: none;
  height:10px;
  position: absolute;
  top:40px;
}

#blabla li {
  display:inline-block;
  margin-left:10px;
  color:white;
  cursor: pointer;
}

#blabla li:hover{
  color:red;
}
.icon{
  display: inline;
  cursor: pointer;
  
}
.searchIcon{
  float:right;
  margin-top:19px;
  margin-right:15px;
  border:3px solid white;
  background: none;
  border-radius:50%;
  width:20px;
  height: 20px;
  

}
.stick{
  width:13px;
  height: 3px;
position: absolute; 
  top:6%;
  right:8px;
   background: white;
   -webkit-transform: rotate(45deg);
           transform: rotate(45deg)
}
.searchbar {
  width:240px;
  height: 30px;
  outline: 0;
  border:none;
  border-radius:40px;
  background-color: rgba(255, 255, 255, 0.856);
  float: right;
  margin:16px;
  margin-right:9px;
  color:rgb(59, 56, 56);
  text-align: center;
  font-family: monospace;
  font-weight: bold;
  font-size: 15px;
}


.boxx {
    width:900px;
    height: 500px;
   
    position: absolute;
    top:62%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display:flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top:20px;


}
.bigtitle {
    color:black;
    display: block;
 text-align: center;
 cursor:pointer;
 margin:15px;
}

.smallbox {
    margin-top:2px;
    width:200px;
    height: 240px;
    border:2px solid rgba(255, 255, 255, 0);
    box-shadow: -2px 0px 12px 1px #000000;
border-radius: 10px;
    
    
}
.smallbox img {
    height: 100%;
    width: 100%;
    border-radius: 10px;
}

.smallbox:hover {
  
    -webkit-transform: scale(1.5);
  
            transform: scale(1.5);
}
.box1 {
    width:900px;
    height: 500px;
    background-color: azure;
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    border:3px solid white;
    
    overflow-y: auto;

}


.movieImg{
    margin-top:18px;
    margin-left:10px;
    width:300px;
    height: 450px;
    background-color: blue;
    border:5px solid rgb(109, 109, 109);
    border-radius: 4px;
    
}
.movieImg img {
    height: 100%;
    width:100%;
}
.titlee h2 {
    border-bottom:5px solid rgba(146, 146, 146, 0.74);
color:white;
}
.info {
    width:500px;
    height: 400px;
    display: inline;
    position: absolute;
    top:15%;
    left:40%;
    font-family: "Helvetica";
    letter-spacing: 1px;
    color:white;
   
}

.info h3 {
    margin-bottom: 0px;
}
